<template>
  <div class="RehabilitationPrint">
    <div>
      <!-- 畫面顯示內容區域 -->
      <h3>{{cData.company_name}}</h3>

      <div class="text-center border-bottom my-2">
        <h1>
          <strong>復健評估表</strong>
        </h1>
      </div>

      <table class="form_title" border="3px #000000 solid;" width="100%">
        <thead>
          <tr>
            <th colspan="2" class="p-2">
              <v-row>
                <v-col cols="2">病歷號: {{uData.p_code}}</v-col>
                <v-col cols="2">床號: {{uData.bed_name}}</v-col>
                <v-col cols="2">姓名: {{uData.name}}</v-col>
                <v-col cols="2">性別: {{uData.sex}}</v-col>
                <v-col cols="2">生日: {{uData.birthday}}</v-col>
                <v-col cols="2">年齡: {{age}}</v-col>
                <!--               <v-col cols="12">疾病史: {{uData.hadadisease}}</v-col> -->
              </v-row>
            </th>
          </tr>
        </thead>

        <tbody
          border="4px #000000 solid;"
          v-for="list in items"
          :key="list.snkey"
        >
          <tr>
            <td colspan="2" class="grey lighten-2 px-4">
              <v-row>
                <v-col cols="3">評估日期: {{list.date}}</v-col>
                <v-col cols="3">評估人: {{list.create_man | filterName}}</v-col>
              </v-row>
            </td>
          </tr>
          <tr>
            <td>1.關節活動度（ROM）</td>
            <td>
              <v-row>
                <v-col cols="3">右上肢:{{list.rom1}}</v-col>
                <v-col cols="3">右下肢:{{list.rom2}}</v-col>
                <v-col cols="3">左上肢:{{list.rom3}}</v-col>
                <v-col cols="3">左下肢:{{list.rom4}}</v-col>
              </v-row>
            </td>
          </tr>

          <tr>
            <td>2.肌肉力量（MMT）</td>
            <td>
              <v-row>
                <v-col cols="3">右上肢:{{list.mmt1}}</v-col>
                <v-col cols="3">右下肢:{{list.mmt2}}</v-col>
                <v-col cols="3">左上肢:{{list.mmt3}}</v-col>
                <v-col cols="3">左下肢:{{list.mmt4}}</v-col>
              </v-row>
            </td>
          </tr>

          <tr>
            <td>3.動作、姿態與步態</td>
            <td>{{list.action}}</td>
          </tr>

          <tr>
            <td>4.肌肉張力(Muscle tone)</td>
            <td>
              <v-row>
                <v-col cols="2">軀幹:{{list.muscletone1}}</v-col>
                <v-col cols="2">右上肢:{{list.muscletone2}}</v-col>
                <v-col cols="2">右下肢:{{list.muscletone3}}</v-col>
                <v-col cols="2">左上肢:{{list.muscletone4}}</v-col>
                <v-col cols="2">左下肢:{{list.muscletone5}}</v-col>
              </v-row>
            </td>
          </tr>

          <tr>
            <td>5.平衡（Balance）</td>
            <td>
              <v-row>
                <v-col cols="4">坐姿靜態:{{list.balance1}}</v-col>
                <v-col cols="4">坐姿動態:{{list.balance2}}</v-col>
                <v-col cols="4">站姿靜態:{{list.balance3}}</v-col>
                <v-col cols="4">站姿靜態:{{list.balance4}}</v-col>
                <v-col cols="8">坐姿到站姿/站姿到坐姿平衡:{{list.balance5}}</v-col>
              </v-row>
            </td>
          </tr>

          <tr>
            <td>6.布郎斯壯分期（Brunnstrom’s stage ）(針對中風個案)</td>
            <td>
              <v-row>
                <v-col cols="2">右上肢-近:{{list.brunnstrom1}}</v-col>
                <v-col cols="2">右上肢-遠:{{list.brunnstrom2}}</v-col>
                <v-col cols="2">左上肢-近:{{list.brunnstrom3}}</v-col>
                <v-col cols="2">左上肢-遠:{{list.brunnstrom4}}</v-col>
                <v-col cols="2">右下肢:{{list.brunnstrom5}}</v-col>
                <v-col cols="2">左下肢:{{list.brunnstrom6}}</v-col>
              </v-row>
            </td>
          </tr>

          <tr>
            <td>7.短期目標（Short term goals）</td>
            <td>{{list.shorttermgoals}}</td>
          </tr>
          <tr>
            <td>8.長期目標（Long term goals）</td>
            <td>{{list.longtermgoals}}</td>
          </tr>
          <tr>
            <td>9.治療項目（Treatment programs）</td>
            <td>{{list.treatmentprograms}}</td>
          </tr>
        </tbody>
      </table>

      <!-- <pre>{{items}}</pre> -->
    </div>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      items: [],
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.items = JSON.parse(sessionStorage.getItem("printData"));
  },

  methods: {},
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";
.RehabilitationPrint {
  .form_title {
    font-size: 1rem;
    font-family: "微軟正黑體";
  }

  td {
    padding: 1px;
    border: 1px #000000 solid;
  }

  tbody .row .col {
    padding: 0px 15px;
    margin: 0;
  }
}

@media print {
  .RehabilitationPrint {
    font-size: 0.6rem;

    .form_title {
      font-size: 0.6rem;
      font-family: "微軟正黑體";
    }

    td {
      padding: 1px;
      border: 1px #000000 solid;
    }

    tbody .row .col {
      padding: 0px 15px;
      margin: 0;
    }

  }

  @page {
    size: A4 portrait;
    margin: 0cm;
    padding: 0cm;
  }
}
</style>